<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title mt-1">
                            {{$t('OnlinePurchase')}}
                        </h5>                        
                        <div>
                            <em id="ancApprove"  v-on:click="UpdateStatus('approved')" checkprivilege="yes" title="Approve" href="javascript:;" class="p-action-btn text-white p-1" style="height:auto;">
                                <i class="fa fa-check"></i>
                                <span class="ml-1">Approve</span>
                            </em>
                            <em id="ancReject"  v-on:click="UpdateStatus('rejected')" checkprivilege="yes" title="Reject" href="javascript:;" class="p-action-btn text-white p-1" style="height: auto;">
                                <i class="fa fa-close"></i>
                                <span class="ml-1">Reject</span>
                            </em>
                        </div>                        
                        <button v-on:click="ClosePOP($event)" href="javascript:;" class="close">
                            <i class="fa fa-times" aria-hidden="true"></i>
                        </button>
                    </div>                                         
                    
                    <div class="modal-body">
                        <div class="listing"> 
                            <tg-list 
                                :showCheckBox="true" 
                                :IsShowAction="false" 
                                :listType="['List']" 
                                ModuleName="Asset"
                                SubModuleCode="Asset"
                                IdentityColumn="purchaseId"                                                    
                                :HeaderText="$t('AssetManagement')"
                                :widgets="widgets"
                                :ListData="PurchaseNewRequisitionList" 
                                :HeaderData="Header"                       
                                :callbackfunction="FeatchData"                                
                                :LegendArray="LegendArray"
                                :SortExp="SortExp"
                                :SortBy="SortBy"
                                :NorecordfoundText="$t('NorecordfoundText')"
                                
                                :TotalRecords="TotalRecord"                                
                                :RenderRowActionMethod="renderActions" 
                                :ListDataCallBackFunction="FeatchData"
                                v-on:PagerButtonClick="pagerMethod" 
                                v-on:HeaderButtonClick="commonHeaderButtonClick">

                                <template v-slot:slotdata="{ data }">                                  
                                    <template v-if="data.column.COLUMN_NAME == 'StatusName'">
                                            <em class="active-disabled" v-if="data.row.status_id==1001">
                                                <span class="btn d-inline-block w-80 text-center text-truncate btn-success">{{data.row.StatusName}}</span>
                                            </em>                                                
                                            <em class="active-disabled" v-else-if="data.row.StatusName=='Approved'">
                                                <span class="btn d-inline-block w-80 text-center text-truncate btn-warning">{{data.row.StatusName}}</span>
                                            </em>                                    
                                             <em class="active-disabled" v-else-if="data.row.StatusName=='Rejected'">
                                                <span class="btn d-inline-block w-80 text-center text-truncate btn-warning">{{data.row.StatusName}}</span>
                                            </em>                                                   
                                            <em v-else class="inactive-disabled">
                                                <span class="btn d-inline-block w-80 text-center text-truncate btn-danger">{{data.row.StatusName}}</span>
                                            </em>
                                    </template>
                                </template>
                        </tg-list>
                        
                            
                            </div>
                        </div>                        
                    </div>                   
                                     
                </div>
            </div>
        </div>
   
    
</template>
    
    <script>
    import DataService from '../../service/DataService'
    export default{
       props:{
        RequisitionData:{
                type:Array,
                required:true,
            }    
       },
        data(){
            return{
                NewRequisitionList:[],
                PurchaseNewRequisitionList:[],
                noRecord:false,
                isLoading:false,                
               
                Header: [
                    {
                    "COLUMN_NAME": "vendor",
                    "DATA_TYPE": "image",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t("Vendor"),
                    "DISPLAY_ORDER": 1,
                    },
                    {
                    "COLUMN_NAME": "quantity",
                    "DATA_TYPE": "image",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t("Quantity"),
                    "DISPLAY_ORDER": 2,
                    },
                    {
                    "COLUMN_NAME": "price",
                    "DATA_TYPE": "image",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t("UnitPrice"),
                    "DISPLAY_ORDER": 3,
                    },
                    {
                    "COLUMN_NAME": "total_Price",
                    "DATA_TYPE": "image",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t("total_price"),
                    "DISPLAY_ORDER": 4,
                    },
                    {
                    "COLUMN_NAME": "purchaseLink",
                    "DATA_TYPE": "image",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t("Link"),
                    "DISPLAY_ORDER": 5,
                    },
                    {
                    "COLUMN_NAME": "StatusName",
                    "DATA_TYPE": "image",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t("Status"),
                    "DISPLAY_ORDER": 6,
                         settings: {
                           isInSlot: true
                        }
                    },
                ],
                listheaderbuttons:[],
                LegendArray:[],            
                widgets:[],            
                noRecord:true,
                TotalRecord:0,
                CurrentPage: 1,
                TotalPages: 0,      
                SortBy: '',
                SortExp: '',
                PageNumber: 1,
                isPaged: 1,
                PageSize: 10,
                }
        },
        created:function(){                           
            this.FeatchData();
        },
        methods:{            
            FeatchData() {                
                var vm = this;
                    vm.isLoading = true;
                    var params = `requisitionId=${vm.RequisitionData.AssetRequisitionId}&type=Quote`;
                    DataService.ViewRequisitionDetail(params).then(response => {                       
                        if (response.data.QuoteNewRequisitionList != null) {                                         
                            if (response.data.QuoteNewRequisitionList.length > 0) {
                                vm.NewRequisitionList = response.data.QuoteNewRequisitionList;
                                vm.PurchaseNewRequisitionList = response.data.PurchaseNewRequisitionList;
                                // vm.TotalRecord = vm.PendingRequestData[0].TOTAL_PAGES;
                                // vm.TotalPage = vm.TotalRecord;
                                // vm.CurrentPage = vm.PageNumber;
                                vm.noRecord = false;                        
                                vm.isLoading = false;
                            }
                            else {
                                vm.NewRequisitionList=[];
                                vm.PurchaseNewRequisitionList = [];
                                vm.noRecord = true;                        
                                vm.isLoading = false;
                            }
                        }
                        setTimeout(function () { vm.CheckBoxBootstrap() }, 100);
                    });
                    vm.isLoading = false;
            },               
            onCancel:function(item){
                this.ClosePOP(item);
            },
            ClosePOP:function(item){               
                this.$emit('ClosePOP',item);
            },
            pagerMethod: function (value) {           
                this.pageSize = value.pageSize;
                this.PageNumber = value.PageNumber;
                this.FeatchData();
            },
            UpdateStatus(status_name){
                var vm = this;
                var val = "";
                $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                    if (val.length > 0)
                        val += ',';
                    val += $(this).val();
                });                
                if (val.length > 0) {              
                    vm.isLoading = true;
                    var postJSON = {
                        Ids: val,
                        category: 'purchase',
                        type: status_name
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataService.UpdateAssetRequisitionQuoteStatus(objectPostString).then((response) => {
                        vm.isLoading = false;                     
                        if (response.data.result=='true') {
                            if(status_name=='approved'){
                                vm.ShowAlert(vm.$t('CommonApprove', [vm.$t('Purchase')]), "success", true, vm.$t("Alert"));
                                vm.FeatchData();
                            }else{
                                vm.ShowAlert(vm.$t('CommonReject', [vm.$t('Purchase')]), "success", true, vm.$t("Alert"));
                                vm.FeatchData();
                            }                         
                                                 
                        } else {
                            if(status_name=='approved'){
                                vm.ShowAlert(vm.$t('CommonApprove', [vm.$t('Purchase')]), "failure", true, vm.$t("Alert"));                               
                            }else{
                                vm.ShowAlert(vm.$t('CommonReject', [vm.$t('Purchase')]), "failure", true, vm.$t("Alert"));
                            }                           
                        }
                    })                   
                }
                else{
                    vm.confirmR(vm.$t('Minimum One Request should be selected.'), true, this.$t('GetScannedList'), function (result) {
                        return false;
                    });
                }
            },
            commonHeaderButtonClick:function(){                
            },
            renderActions() {                
            },
        }
    }
    </script>