<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title mt-1">
                            {{ $t('Quote') }}
                        </h5>
                        <div>
                            <em v-show="isVisibleButton == true" :disabled="isVisibleButton == true" id="ancApprove"
                                v-on:click="UpdateStatus('approved')" checkprivilege="yes" title="Approve"
                                href="javascript:;"
                                :class="{ 'p-action-btn text-white p-1 disabled': UserType == 'CL', 'p-action-btn text-white p-1': UserType != 'CL' }"
                                style="cursor:pointer;">
                                <i class="fa fa-check"></i>
                                <span class="ml-1">Approve</span>
                            </em>
                            <em v-show="isVisibleButton == true" :disabled="isVisibleButton == true" id="ancReject"
                                v-on:click="UpdateStatus('rejected')" checkprivilege="yes" title="Reject"
                                href="javascript:;"
                                :class="{ 'p-action-btn text-white p-1 disabled': UserType == 'CL', 'p-action-btn text-white p-1': UserType != 'CL' }"
                                style="cursor:pointer;">
                                <i class="fa fa-close"></i>
                                <span class="ml-1">Reject</span>
                            </em>

                            <em v-show="isVisibleButton == false" :disabled="isVisibleButton == false" id="ancApprove"
                                title="Approve" href="javascript:;" class="p-action-btn text-white p-1 disabled"
                                style="cursor:pointer;">
                                <i class="fa fa-check"></i>
                                <span class="ml-1">Approve</span>
                            </em>
                            <em v-show="isVisibleButton == false" :disabled="isVisibleButton == false" id="ancReject"
                                title="Reject" href="javascript:;" class="p-action-btn text-white p-1 disabled"
                                style="cursor:pointer;">
                                <i class="fa fa-close"></i>
                                <span class="ml-1">Reject</span>
                            </em>


                        </div>
                        <button v-on:click="ClosePOP($event)" href="javascript:;" class="close">
                            <i class="fa fa-times" aria-hidden="true"></i>
                        </button>
                    </div>
                    <div class="modal-body">
                        <div class="listing">
                            <div class="table-responsive table-fix-header">
                                <table class="table table-bordered dt-responsive nowrap mt-0" id="tblUniqueItems">
                                    <thead class="thead-bg">
                                        <tr>
                                            <th class="text-center">
                                                <div class="custom-control custom-checkbox">
                                                    <input id="chkAllUniqueItems" class="custom-control-input"
                                                        type="checkbox" name="checkbox" v-model='allSelected'
                                                        @click="selectAll($event)">
                                                    <label
                                                        class="custom-control-label universal-custom-control-label pt-1"
                                                        for="chkAllUniqueItems"></label>
                                                </div>
                                            </th>
                                            <th class="text-center">{{ $t('Vendor') }}</th>
                                            <th class="text-center">{{ $t('Quantity') }}</th>
                                            <th class="text-center">{{ $t('UnitPrice') }}</th>
                                            <th class="text-center">{{ $t('TotalPrice') }}</th>
                                            <th class="text-center">{{ $t('Quote') }}</th>
                                            <th class="text-center">{{ $t('Status') }}</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr v-for="(item, index) in NewRequisitionList" :key="index">
                                            <td class="text-center">
                                                <div class="custom-control custom-checkbox">
                                                    <input class="custom-control-input" type="checkbox" name="checkbox"
                                                        v-model="item.isSelected"
                                                        :disabled="item.isCheckBoxDisabled ? true : false"
                                                        v-on:change="singleSelect($event, item)">

                                                    <label
                                                        class="custom-control-label universal-custom-control-label pt-1"></label>
                                                </div>
                                            </td>
                                            <td class="text-center">{{ item.vendor }}</td>
                                            <td class="text-center">{{ item.quantity }}</td>
                                            <td class="text-center">{{ item.price }}</td>
                                            <td class="text-center">{{ item.total_Price }}</td>
                                            <td class="text-center">{{ item.quote }}</td>

                                            <td class="text-center" v-if="item.StatusName === 'Active'">
                                                <span
                                                    class="btn d-inline-block w-80 h-20 text-center text-truncate btn-success">{{
            item.StatusName
        }}</span>
                                            </td>

                                            <td class="text-center"
                                                v-else-if="item.StatusName === 'Approved' || item.StatusName === 'Rejected'">
                                                <span
                                                    class="btn d-inline-block w-80 text-center text-truncate btn-warning">{{
            item.StatusName }}</span>
                                            </td>

                                            <td class="text-center" v-else>
                                                <span
                                                    class="btn d-inline-block w-80 text-center text-truncate btn-danger">{{
                                                    item.StatusName }}</span>
                                            </td>

                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>

                </div>

            </div>
        </div>
    </div>


</template>

<script>
import DataService from '../../service/DataService'
export default {
    props: {
        RequisitionData: {
            type: Array,
            required: true,
        }
    },
    data() {
        return {
            NewRequisitionList: [],
            PurchaseNewRequisitionList: [],
            noRecord: false,
            isLoading: false,
            widgets: [],
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            SortBy: '',
            SortExp: '',
            PageNumber: 1,
            isPaged: 1,
            PageSize: 10,
            UserType: '',
            allSelected: false,
            passQuoteIds: '',
            isVisibleButton: false
        }
    },

    created: function () {
        this.getQuoteRequisitionList();
        this.UserType = this.GetUserInfo.UserType;
    },
    methods: {
        getQuoteRequisitionList() {
            var vm = this;
            vm.isLoading = true;
            var params = `requisitionId=${vm.RequisitionData.AssetRequisitionId}&type=Quote`;
            DataService.ViewRequisitionDetail(params).then(response => {
                if (response.data.QuoteNewRequisitionList != null) {
                    vm.isLoading = false;
                    if (response.data.QuoteNewRequisitionList.length > 0) {
                        vm.NewRequisitionList = response.data.QuoteNewRequisitionList;
                        vm.PurchaseNewRequisitionList = response.data.PurchaseNewRequisitionList;
                        vm.NewRequisitionList.forEach((row) => {
                            row.isCheckBoxDisabled = false;
                            row.isSelected = false;
                            if (row.StatusName == "Approved" || row.StatusName == "Rejected") {
                                row.isCheckBoxDisabled = true;
                            }
                        })

                    }
                    else {
                        vm.NewRequisitionList = [];
                        vm.isLoading = false;
                    }
                }
            });
            vm.isLoading = false;
        },
        onCancel: function (item) {
            this.ClosePOP(item);
        },
        ClosePOP: function (item) {
            this.$emit('ClosePOPuP', item);
        },

        selectAll(e) {

            var vm = this;
            let obj = {};
            let quoteDetails = []

            vm.allSelected = !vm.allSelected;
            vm.isVisibleButton = !vm.isVisibleButton


            if (vm.allSelected) {
                vm.isVisibleButton = true;
                vm.NewRequisitionList.forEach(ele => {

                    if (ele.isCheckBoxDisabled == true) {
                        ele.isSelected = false;
                    }
                    else {
                        ele.isSelected = true;
                    }


                })
            } else {
                vm.NewRequisitionList.forEach(element => {
                    element.isSelected = false;
                })
            }


            vm.NewRequisitionList.forEach(ele => {

                if (ele.isSelected == true) {
                    obj = {
                        quoteId: ele.quoteId,
                        vendorName: ele.vendor
                    }

                    quoteDetails.push(obj);
                }
            })

            vm.passQuoteIds = quoteDetails.map(item => item.quoteId).join(',');
            if (vm.passQuoteIds == '') {
                vm.isVisibleButton = false;
            }

        },


        singleSelect(event, item) {

            var vm = this;
            let obj = {};
            let quoteDetails = []

            if (event.target.checked == true) {

                vm.isVisibleButton = true;

                vm.NewRequisitionList.forEach(element => {

                    if (element.isCheckBoxDisabled == true) {
                        element.isSelected = false;

                    } else {
                        //
                        if (element.quoteId == item.quoteId) {
                            element.isSelected = true;
                            vm.allSelected = false;
                        }


                    }


                })
            }
            else {

                vm.NewRequisitionList.forEach(element => {
                    if (element.quoteId == item.quoteId) {
                        element.isSelected = false;
                        vm.allSelected = false;
                    }
                })


            }

            vm.NewRequisitionList.forEach(ele => {

                if (ele.isSelected == true) {
                    obj = {
                        quoteId: ele.quoteId,
                        vendorName: ele.vendor
                    }

                    quoteDetails.push(obj);
                }
            })

            let checklength = vm.NewRequisitionList.filter(x => x.isCheckBoxDisabled == false).length;
            vm.passQuoteIds = quoteDetails.map(item => item.quoteId).join(',');

            if (vm.passQuoteIds == '') {
                return vm.isVisibleButton = false;
            }

            if (checklength == quoteDetails.length) {
                vm.allSelected = true;
                vm.isVisibleButton = true
            }
            else {

                vm.allSelected = false;
            }

        },

        UpdateStatus(status_name) {

            var vm = this;

            if (vm.passQuoteIds == '') {
                return;
            }

            vm.isLoading = true;
            // vm.confirmR(vm.$t('Are you sure you want to delete these record(s)?'), true, false, function (result) {

            var postJSON = {
                Ids: vm.passQuoteIds,
                category: 'quote',
                type: status_name
            };

            var objectPostString = JSON.stringify(postJSON);
            DataService.UpdateAssetRequisitionQuoteStatus(objectPostString).then((response) => {
                vm.isLoading = false;
                if (response.data.result == 'true') {
                    if (status_name == 'approved') {
                        vm.allSelected = false;
                        vm.isVisibleButton = false;

                        vm.ShowAlert(vm.$t('CommonApprove', [vm.$t('Quote')]), "success", true, vm.$t("Alert"));
                        vm.getQuoteRequisitionList();
                    } else {
                        vm.allSelected = false;
                        vm.isVisibleButton = false;
                        vm.ShowAlert(vm.$t('CommonReject', [vm.$t('Quote')]), "success", true, vm.$t("Alert"));
                        vm.getQuoteRequisitionList();
                    }

                } else {
                    if (status_name == 'approved') {
                        vm.allSelected = false;
                        vm.isVisibleButton = false;
                        vm.ShowAlert(vm.$t('CommonApprove', [vm.$t('Quote')]), "failure", true, vm.$t("Alert"));
                    } else {
                        vm.allSelected = false;
                        vm.isVisibleButton = false;
                        vm.ShowAlert(vm.$t('CommonReject', [vm.$t('Quote')]), "failure", true, vm.$t("Alert"));
                    }
                }
            })

            // });


        },

    }
}
</script>

<style scoped>
input.custom-control-input {
    z-index: 1;
}

span.btn.d-inline-block.w-80.text-center.text-truncate.btn-warning {
    padding: 0;
}

span.btn.d-inline-block.w-80.h-20.text-center.text-truncate.btn-success {
    padding: 0px;
}

.p-action-btn {
    border-left: 0px solid #4d5a60;
}
</style>