<template>
    <section>
        <div class="col-12 listing tblassestgroupinfo px-0">
            <div class="col-0 productNote">
                <small v-if="IsProduct == 'ProductName'">{{ $t('NoteUniqueItems') }}</small>
                <small v-if="IsProduct == 'Product'">{{ $t('UniqueItems') }}</small>
            </div>
            <div class="table-responsive">
                <table id="tblassestgroupinfo" class="table table-bordered dt-responsive nowrap mt-0">
                    <thead class="thead-bg">
                        <tr>
                            <th class="text-center">
                                <span>{{ $t('SerialNumber') }}</span>
                            </th>
                            <th class="text-center"><span>{{ $t('ItemName') }}</span></th>
                            <th class="text-center"><span>{{ $t('PairProduct') }}</span>
                            </th>
                            <th class="text-center"><span>{{ $t('ItemMandatoryForCheckOut') }}</span>
                            </th>
                            <th class="text-center">
                                <span>{{ $t('Action') }}</span>
                            </th>
                        </tr>
                    </thead>

                    <tbody>

                        <tr v-for="(item, index) in ProductData" :key="index">
                            <td class="text-center">
                                {{ index + 1 }}
                            </td>
                            <td>{{ item.Name }}</td>
                            <td v-if="CreateUniqueNameListing == false" class="text-center">

                                <select class="form-control" ref="barcode" :class="{
                    'is-invalid': item.isValidation ? false : true

                }" v-on:change="bindProductItem($event, item.count)">
                                    <option value="">Select Product Name</option>
                                    <option v-for="(item, itemIndex) in AssetData" :key="itemIndex" :value="item.Value">
                                        {{ item.Text }}
                                    </option>
                                </select>


                            </td>


                            <td v-else class="text-center">


                                <Field name="Item Name" :rules="{ 'required': true }" label="Item Name"
                                    v-slot="{ errors }" class="d-flex pair_select"
                                    v-for="nitem  in item.PairProductDetails" :key="nitem.index">

                                    <div class="input-group" style="position: relative;">
                                        <input id="txtAssetItems" name="Item Name"
                                            @keyup="Filterditems(item.count, nitem.pairProductName)"
                                            v-model="nitem.pairProductName" placeholder="Enter Product Name"
                                            class="form-control" type="text"
                                            v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }">
                                        <!-- <span v-if="errors[0]" name="Item Name" class="invalid-feedback d-block">{{
                                            errors[0] }}</span> -->
                                        <div class="unique_dynamicdatalist" v-show="isDropdownloaded == true">
                                            <ul class="listOfProduct" v-if="item.IsDDLShowHide">
                                                <li class="listitem all-items" v-for="(it, index) in UniqueItemData1"
                                                    v-on:click="bindUniqueItem($event, item.count)" :value="it.Value"
                                                    :key="index">
                                                    {{ it.Name }}</li>
                                            </ul>
                                        </div>
                                    </div>

                                    <a class="btn float-left" href="javascript:;"
                                        v-on:click="itemDDLShowHide(item, item.count)"
                                        style="background-color: #fff;color: #303030;border-color: #abb5bf;border-left: none;max-height: 37px;"
                                        id="customddlinput">
                                        <em class="dropdown-toggle float-right"></em>
                                    </a>
                                </Field>


                            </td>


                            <td class="text-center">
                                <div class="form-group mb-0">
                                    <label class="switch">
                                        <input id="isCheck" type="checkbox" v-model="item.IsMandatory"
                                            v-on:change="ChangeCheck($event, ...item.PairProductDetails)"
                                            class="form-check-input rdoticketfor custom-control-input dynamic" />
                                        <span class="slider round">
                                            <span class="slider-yes">Yes</span>
                                            <span class="slider-no">No</span>
                                        </span>
                                    </label>
                                </div>
                            </td>

                            <td class="text-center single-action">

                                <a v-if="parseInt(result) == parseInt(index + 1)" href="javascript:;" id="addProduct"
                                    title="Add Product" @click="AddMoreItem(item, ...item.PairProductDetails);"><i
                                        class="fa fa-plus px-4 text-success"></i></a>

                                <a href="javascript:;" title="Delete" @click="deleteItem(item.count)"><i
                                        class="fa fa-trash px-4 text-danger"></i></a>
                            </td>
                        </tr>

                        <tr v-if="NoRecordLength == 0">
                            <td colspan="7" class="text-danger text-center no-record">No Record
                                Found</td>
                        </tr>

                    </tbody>

                </table>
            </div>
            <div v-if="ProductData.length > 0" class="col-12 text-right mt-3">
                <a v-on:click="SubmitData()" href="javascript:;" title="Save" class="btn btn-success"><i
                        class="fa fa-save pr-2"></i>{{ $t('Save') }} </a>&nbsp;
                <a href="javascript:;" title="Cancel" @click="RelationPopUpClose" id="ancCancelgroupRequisition"
                    class="btn btn-danger"><i class="fa fa-close pr-2"></i>{{ $t('Cancel')
                    }}</a>
            </div>
        </div>
    </section>
</template>
<script>
import DataService from '../../service/DataService';
export default {
    props: {
        UniqueData: {
            type: Array,
            required: true
        },
        ProductData: {
            type: Array
        },
        LocationValue: {
            type: Number
        },
        txtAssetType: {
            type: Number
        },
        iselectVal: {
            type: Number
        },
        ClosePopup: {
            type: Function
        },
        LocationValueForBarCode: {
            type: Number,
        },
        txtBarcode: {
            type: String
        },
        FetchData: {
            type: Function
        },
        AssetData: {
            type: Array
        },
        IsProduct: {
            type: String
        },
        assignTillDate: {
            type: String
        },
        checkItem: {
            type: Number
        },
        AssetCatalogueId: {
            type: Number
        }
        , CreateUniqueNameListing: {

        }
    },
    data() {
        return {
            NoRecordLength: 0,
            noDataAvailable: 'No Product is available in the items list.',
            isCheckDeployItem: '',
            product: {},
            Relations: [],
            sliderValue: 0,
            IsMandatory: false,
            ChildId: null,
            result: 1,
            isVal: false,
            UniqueItemData1: [],
            IsDDLShowHide: false,
            assetUniqueItemValue: "",
            filterRecodes: [],
            isDropdownloaded: false
        }
    },


    created: function () {
        var vm = this;
        if (vm.assignTillDate != null || vm.checkItem != null) {
            vm.isVisible = false;
            vm.$parent.searchResult = false;
            return vm.ShowAlert(
                vm.$t(`Product is already assigned to someone untill  ${vm.assignTillDate}`),
                "warning",
                vm.$t("Alert"))
        }
        else {

            vm.isVisible = true;
            vm.NoRecordLength = vm.ProductData.length;
            vm.UniqueItemData1 = vm.UniqueData;
        }

        if (vm.UniqueItemData1.length > 0) {
            let PairProduct = [];
            let obj = {
                pairProductName: '',
                pairProductId: null
            }
            PairProduct.unshift(obj);

            vm.ProductData.forEach(ele => {
                ele.PairProductDetails = PairProduct;

            })
        }
        else {
            /// 
            return
        }

    },



    updated() {
        var vm = this;
        vm.UniqueItemData1 = vm.UniqueData;
        vm.lastItem();

    },



    methods: {

        lastItem() {
            var vm = this;
            vm.result = this.ProductData.length;
            vm.NoRecordLength = this.ProductData.length;


        },

        itemDDLShowHide(item, count) {
            const selectedItem = this.ProductData.find(ele => ele.count === count);
            if (selectedItem) {
                selectedItem.IsDDLShowHide = !selectedItem.IsDDLShowHide;
                this.isDropdownloaded = !this.isDropdownloaded;
            }
        },


        uniqueItemChange: function (item, count) {
            var vm = this;
            vm.iselectValue = item.Value;
            vm.assetUniqueItemValue = item.Name;

            const selectedItem = this.ProductData.find(ele => ele.count === count);
            if (selectedItem) {
                selectedItem.IsDDLShowHide = !selectedItem.IsDDLShowHide;
                this.isDropdownloaded = !this.isDropdownloaded;
            }

        },

        async Filterditems(count, assetUniqueItemValue) {
            let vm = this;
            vm.filterRecodes = [];
            vm.isLoading = true;
            vm.isDropdownloaded = false;

            let url = '';
            url = `assetCatalogId=&name=${assetUniqueItemValue}&issueTo=&locationId=${vm.LocationValue ? vm.LocationValue : vm.LocationValueForBarCode}&requestFrom=addAssetRelationShip&assetType=`;
            await DataService.GetUniqueAssetItems(url).then(function (response) {
                vm.filterRecodes = response.data;
                vm.SuniqueItemData = response.data;
                if (vm.filterRecodes != null) {
                    if (vm.filterRecodes.length == 1) {
                        vm.IsDDLShowHide = true;
                        vm.uniqueItemChange(...vm.filterRecodes, count);
                    } else {
                        vm.UniqueItemData1 = [];
                        vm.UniqueItemData1 = vm.filterRecodes;

                        const selectedItem = vm.ProductData.find(ele => ele.count === count);
                        if (selectedItem) {
                            selectedItem.IsDDLShowHide = !selectedItem.IsDDLShowHide;
                            vm.isDropdownloaded = true;
                        }

                    }
                    vm.isLoading = false;
                } else {
                    vm.isLoading = false;
                }
            })


        },


        ChangeCheck(event, pitem) {
            debugger;
            var vm = this;
            vm.IsMandatory = false;
            if (event.target.checked == true) {
                if (vm.ChildId != null) {
                    vm.Relations.forEach(element => {
                        var itemExist = vm.Relations.some(x => x.ChildId === pitem.pairProductId)
                        if (itemExist == true) {
                            element.IsMandatory = true;
                        }
                        else {
                            element.IsMandatory = false;
                        }
                    })
                    vm.IsMandatory = false;
                }
                return;
            }
            else {
                return vm.IsMandatory = false;
            }
        },


        AddMoreItem(item, pitem) {

            var vm = this;

            let PairProduct = [];

            let obj = {
                pairProductName: '',
                pairProductId: null
            }
            PairProduct.push(obj);
            let product = {}


            if (pitem.pairProductId != null) {

                if (vm.iselectVal != null) {


                    if (vm.ProductData.length > 1) {
                        product = {
                            Name: item.Name,
                            count: vm.ProductData.length + 1,
                            isValidation: false,
                            PairProductDetails: PairProduct,
                            IsDDLShowHide: false

                        }
                    }
                    else {

                        product = {
                            Name: vm.CreateUniqueNameListing == false ? item.AssetCatalogName : item.AssetCatalogName +
                                '(' + item.AssetUniqueName + ')',
                            count: vm.ProductData.length + 1,
                            isValidation: false,
                            PairProductDetails: PairProduct,
                            IsDDLShowHide: false

                        }

                    }
                    vm.ProductData.push(product);
                    vm.ChildId = null;
                    return;
                }
                else {

                    if (vm.ProductData.length > 1) {
                        product = {
                            Name: item.Name,
                            count: vm.ProductData.length + 1,
                            isValidation: false,
                            PairProductDetails: PairProduct,

                        }
                    }

                    else {

                        product = {
                            Name: vm.CreateUniqueNameListing == false ? item.AssetCatalogName : item.AssetCatalogName +
                                '(' + item.AssetUniqueName + ')',
                            count: vm.ProductData.length + 1,
                            isValidation: false,

                        }

                    }

                    vm.ProductData.push(product);
                    vm.ChildId = null;
                }
            }
            else {
                // vm.ProductData.map(ele => {
                //     if (ele.count == 1) {
                //         return ele.isValidation = false;
                //     }

                // })
                vm.ShowAlert(
                    vm.$t("Product Name is required."),
                    "failure",
                    vm.$t("Alert")
                );
            }
        },


        bindProductItem(value, count) {

            var vm = this;
            var temp = [];
            let isChildExit = false;

            vm.ChildId = parseInt(value.target.value);

            let PairProduct = [];
            let matchedObject = vm.AssetData.find(x => x.Value == value.target.value);

            let obj = {
                pairProductName: matchedObject.Text,
                pairProductId: parseInt(matchedObject.Value)
            }
            PairProduct.unshift(obj);
            vm.ProductData.forEach(ele => {
                if (ele.count == count) {
                    ele.PairProductDetails = PairProduct;
                }
            })

            if (count == 1) {


                vm.Relations = []
                if (vm.Relations.length == 0) {

                    let childId = { ChildId: vm.ChildId, IsMandatory: false }
                    vm.Relations.unshift(childId);
                    vm.ProductData.map(ele => {
                        if (ele.count == count) {
                            return ele.isValidation = true;
                        }

                    })

                    return;

                }
            }
            temp.push(vm.Relations);

            temp.forEach(ele => {
                isChildExit = vm.Relations.some(x => x.ChildId === parseInt(value.target.value));
                if (isChildExit == false) {
                    let childId = { ChildId: vm.ChildId, IsMandatory: false }
                    vm.Relations.push(childId);
                    vm.ProductData.forEach(ele => {
                        if (ele.count == count) {
                            ele.isValidation = true;
                        }
                    })
                    return;
                }
                else {
                    value.target.value = null;
                    vm.ChildId = null;
                    return vm.ShowAlert(
                        vm.$t("Relation with this Product Has already been added."),
                        "failure",
                        vm.$t("Alert")
                    );
                }

            })
        },



        bindUniqueItem(event, count) {


            var vm = this;
            var temp = [];
            let isChildExit = false;
            let PairProduct = [];

            let matchedObject = vm.UniqueItemData1.find(x => x.Value == event.target.value);

            vm.ChildId = parseInt(event.target.value);


            if (count == 1) {

                if (matchedObject != undefined) {

                    vm.ProductData.forEach(ele => {
                        if (ele.count == count) {
                            ele.PairProductDetails.forEach(ele => {
                                ele.pairProductId = parseInt(matchedObject.Value),
                                    ele.pairProductName = matchedObject.Name
                            })
                        }
                    })

                }
                else {
                    return;
                }

                vm.Relations = [];

                if (vm.Relations.length == 0) {

                    let childId = { ChildId: vm.ChildId, IsMandatory: false }
                    vm.Relations.unshift(childId);
                    vm.ProductData.map(ele => {
                        if (ele.count == count) {
                            return ele.isValidation = true;
                        }

                    })

                    const selectedItem = vm.ProductData.find(ele => ele.count === count);

                    if (selectedItem) {
                        selectedItem.IsDDLShowHide = !selectedItem.IsDDLShowHide;
                        vm.isDropdownloaded = false;
                    }

                    return;

                }
            }

            let obj = {
                pairProductName: matchedObject.Name,
                pairProductId: parseInt(matchedObject.Value)
            }

            PairProduct.unshift(obj);

            vm.ProductData.forEach(ele => {
                if (ele.count == count) {
                    ele.PairProductDetails = PairProduct;
                }
            })

            temp.push(vm.Relations);

            temp.forEach(ele => {
                isChildExit = vm.Relations.some(x => x.ChildId === parseInt(event.target.value));
                if (isChildExit == false) {
                    let childId = { ChildId: vm.ChildId, IsMandatory: false }
                    vm.Relations.push(childId);
                    vm.ProductData.forEach(ele => {
                        if (ele.count == count) {
                            ele.isValidation = true;
                        }
                    })

                    const selectedItem = vm.ProductData.find(ele => ele.count === count);

                    if (selectedItem) {
                        selectedItem.IsDDLShowHide = !selectedItem.IsDDLShowHide;
                        vm.isDropdownloaded = false;
                    }


                    return;
                }
                else {

                    event.target.value = '';  // empty v model values ;
                    vm.ChildId = null;


                    return vm.ShowAlert(
                        vm.$t("Relation with this Product Has already been added."),
                        "failure",
                        vm.$t("Alert")
                    );
                }

            })


        },
        deleteItem(value) {
            var vm = this;
            vm.confirmR(vm.$t('Are you sure you want to delete these record(s)?'), true, false, function (result) {
                const index = vm.ProductData.findIndex(x => x.count === value);
                if (index !== -1) {
                    vm.ProductData.splice(index, 1);
                    // this.$parent.ClearhdnIdsOnChange();
                }

                vm.ShowAlert(
                    vm.$t("Item has been deleted Successfully "),
                    "success",
                    vm.$t("Alert")
                );

                if (vm.ProductData.length == 0) {
                    vm.UniqueItemData1 = [];
                    return vm.$parent.searchResult = false;
                }

            });
        },

        SubmitData: function () {

            var vm = this;
            let obj = {};
            if (this.LocationValueForBarCode == "" || vm.LocationValueForBarCode == null) {
                if (vm.Relations.length == 0) {

                    vm.ProductData.forEach(ele => {
                        if (ele.count == 1) {
                            return ele.isValidation = false;
                        }

                    });

                    return vm.ShowAlert(
                        vm.$t("Product Name is required."),
                        "warning",
                        vm.$t("Alert"))

                }
                obj = {
                    "ParentId": this.txtAssetType ? parseInt(this.txtAssetType) : this.iselectVal,
                    "type": this.txtAssetType ? "ProductName" : "Product",
                    "LocationId": this.LocationValue,
                    "Relations": vm.Relations
                }
            }
            else if (vm.Relations.length <= 0) {
                return vm.ShowAlert(vm.$t("Pair Product Name is required."), "failure", vm.$t("Alert"))
            }
            else {


                obj = {
                    "ParentId": this.AssetCatalogueId,
                    "type": "ProductName",
                    "LocationId": this.LocationValueForBarCode,
                    "Relations": vm.Relations
                }
            }
            DataService.SaveAssetRelationship(obj).then(response => {

                if (response.data != null) {

                    var result = response.data;
                    if (result.status == 0) {
                        this.RelationPopUpClose();
                        return vm.ShowAlert(
                            vm.$t("Add relationship Submitted successfully "),
                            `${result.msg}`,
                            vm.$t("Alert")
                        );
                    }
                    else {
                        vm.ShowAlert(
                            vm.$t("Something went wrong"),
                            `${result.msg}`,
                            vm.$t("Alert")
                        );
                    }
                }

            });
        },
        RelationPopUpClose() {
            this.$parent.ClosePopup();
        }
    }
}
</script>
<style>
#tblassestgroupinfo .pair_select {
    max-width: 278px;
}

.table-responsive .unique_dynamicdatalist {
    margin-bottom: 3px;
    position: unset;
}

#tblassestgroupinfo .pair_select .form-control {
    height: 37.2px !important;
}

input#isCheck:checked+.slider {
    text-indent: 0 !important;
}
</style>