<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <div class="my-popups">
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-header d-flex">
                            <h5 class="modal-title">
                                {{ $t('AddRelationship') }}
                            </h5>
                            <button v-on:click="ClosePopup()" type="button" class="close" title="Close"><em
                                    class="fa fa-times"></em></button>
                            <span class="user-guide ">
                                <a class="ancuserguide text-white" data-toggle="collapse" href="#collapseReplyUG"
                                    role="button" aria-expanded="false" aria-controls="collapseExample"> <em
                                        class="fa fa-files-o pr-2"></em>User Guide</a>
                                <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
                                    <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                                        <div class="con" v-html="$t('USERGUIDE_AC_ADD_RELATIONSHIP')"></div>
                                    </div>
                                </div>
                            </span>
                        </div>
                        <div class="modal-body">
                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0">
                                <div class="row">
                                    <div class="col-12">
                                        <div class="rad_value" type="radiobutton">
                                            <div class="custom-control custom-radio custom-control-inline">
                                                <input type="radio" class="custom-control-input dynamic IssuedByIds"
                                                    id="SearchProductName" @change="searchByProduct('barcode')"
                                                    :checked="SearchByBarcode" name="SearchProduct"
                                                    value="CheckedCheckBox">
                                                <label class="custom-control-label" for="SearchProductName">{{
            $t('SearchbyBarcodeRFID') }}</label>

                                            </div>
                                            <div class="custom-control custom-radio custom-control-inline">
                                                <input type="radio" class="custom-control-input dynamic IssuedByIds"
                                                    id="SearchProducts" @change="searchByProduct('product')"
                                                    :checked="!SearchByBarcode" name="SearchProduct"
                                                    value="CheckedCheckBox">
                                                <label class="custom-control-label" for="SearchProducts">{{
            $t('SearchbyProduct') }}</label>
                                            </div>
                                        </div>
                                    </div>

                                    <template v-if="SearchByBarcode">
                                        <Form ref="ProductSearch" class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0">
                                            <div class="col-12 float-left mt-3 SearchByBarcode p-0">
                                                <div class="col-12 col-sm-12  float-left">
                                                    <div class="form-group mb-3">
                                                        <label><span id="">{{ $t('Searchby') }}
                                                                {{ $t('Barcode') }}/{{ $t('SerialNumber') }}/{{
            $t('RFID')
        }}:</span></label><span class="text-danger">*</span>

                                                        <Field name="BarCode" class="w-100"
                                                            :rules="{ 'required': true }" v-slot="{ errors }">
                                                            <input name="BarCode" placeholder="Enter Barcode"
                                                                v-model="txtBarcode" @keyup="GetLocationDataByBarcode()"
                                                                :class="{ 'form-control': true, 'is-invalid': errors[0] }"
                                                                type="text">
                                                            <span v-if="errors[0]" name="BarCode"
                                                                class="invalid-feedback d-block">{{
            errors[0] }}</span>
                                                        </Field>
                                                    </div>
                                                </div>
                                            </div>
                                            <div class="col-9 float-left searchByProduct">
                                                <div class="form-group">
                                                    <label for="Subject">{{ $t('Location') }}:<span
                                                            class="text-danger">*</span></label>
                                                    <Field name="Location" class="w-100" :rules="{ 'required': true }"
                                                        v-slot="{ errors }">
                                                        <select name="Location" v-model="LocationValueForBarCode"
                                                            v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }">
                                                            <option value="">{{ $t('Select') }}
                                                            </option>
                                                            <option :disabled="LocationValueForBarCode"
                                                                v-if="LocationValueForBarCode != ''"
                                                                :value="LocationValueForBarCode">
                                                                {{ LocationName }}
                                                            </option>
                                                        </select>
                                                        <span v-if="errors[0]" name="Location"
                                                            class="invalid-feedback d-block">{{
            errors[0] }}</span>
                                                    </Field>
                                                </div>
                                            </div>
                                            <div
                                                class="col-sm-6 col-md-6 col-lg-3 float-left pl-md-0 mt-0 mt-md-4 mt-sm-4">
                                                <div class="background-0 text-left w-100 form-check-inline">
                                                    <input type="hidden" id="hdnSearch" value="">
                                                    <a title="Search" id="Searchassest_barcode" href="javascript:;"
                                                        v-on:click="SearchAssetByBarCode()"
                                                        class="btn btn-success w-50 mr-1 px-4 hlsearch"
                                                        data-toggle-tooltip="tooltip" data-original-title="Search"><em
                                                            class="fa fa-search"></em></a>
                                                    <a id="aClear" href="javascript:;" title="Clear all "
                                                        v-on:click="ClearhdnIdsOnChange()"
                                                        class="btn btn-danger w-50 clearsearchtext px-4"
                                                        data-toggle-tooltip="tooltip" data-original-title="Clear"><em
                                                            class="fa fa-refresh"></em></a>
                                                </div>
                                            </div>
                                        </Form>
                                    </template>
                                    <template v-if="!SearchByBarcode">
                                        <Form ref="ProductSearchBar"
                                            class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0">
                                            <div class="col-12 float-left searchByProduct">
                                                <div class="form-group">
                                                    <label>{{ $t('Location') }}<span
                                                            class="text-danger">*</span></label>
                                                    <Field name="Location" class="w-100" :rules="{ 'required': true }"
                                                        v-slot="{ errors }">
                                                        <tree-view v-model="TreeValue" :options="options"
                                                            v-on:input="BindItemTypeByUserId(TreeValue)"
                                                            placeholder="Select Location" :show-count="true"
                                                            :flat="true"
                                                            :class="{ 'form-control': true, 'is-invalid': errors[0] }">
                                                        </tree-view>
                                                        <span v-if="errors[0]" name="Location"
                                                            class="invalid-feedback d-block">{{ errors[0] }}</span>
                                                    </Field>
                                                </div>
                                            </div>
                                            <div id="searchByProduct" class="col-12 float-left">
                                                <div class="col-12 float-left pl-0 py-2">
