                                                                :checked="IsProductName" value="ProductName">
                                                            <label class="custom-control-label" for="ProductNameItem">{{
            $t('AssetId') }}</label>
                                                        </div>
                                                        <div class="custom-control custom-radio custom-control-inline">
                                                            <input type="radio" class="custom-control-input dynamic"
                                                                id="ItemName" @change="searchByItemName('Product')"
                                                                :checked="!IsProductName" value="Product">
