            $t('ItemName')
        }}</label>
                                                        </div>
                                                    </div>
                                                </div><br />
                                                <div class="col-9 float-left pl-0" v-if="IsProductName">
                                                    <div class="form-group">
                                                        <label>{{ $t('ProductName') }}:<span
                                                                class="text-danger">*</span></label>
                                                        <Field name="Product" class="w-100"
                                                            :rules="{ 'required': true }" v-slot="{ errors }">
                                                            <select name="AssetTypeData" v-model="txtAssetType"
                                                                v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }">
                                                                <option value="">Select</option>
                                                                <option v-for="(item, itemIndex) in AssetTypeData"
                                                                    :key="itemIndex" :value="item.Value">{{ item.Text }}
                                                                </option>
                                                            </select>
                                                            <span v-if="errors[0]" class="invalid-feedback d-block">{{
            errors[0] }}</span>
                                                        </Field>
                                                    </div>
                                                </div>
                                                <div class="col-9 float-left pl-0" v-if="!IsProductName">
                                                    <div class="form-group">
                                                        <label>{{ $t('ItemName') }}:<span
                                                                class="text-danger">*</span></label>
                                                        <Field name="Item Name" :rules="{ 'required': true }"
                                                            label="Item Name" v-slot="{ errors }"
                                                            class="d-flex pair_select">
                                                            <div class="input-group mb-3" style="position: relative;">
                                                                <input id="txtAssetItems" name="Item Name"
                                                                    @keyup="Filterditems" v-model="assetUniqueItem"
                                                                    placeholder="Enter Product Name"
                                                                    class="form-control" type="text"
                                                                    v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }">
                                                                <span v-if="errors[0]" name="Item Name"
                                                                    class="invalid-feedback d-block">{{ errors[0]
                                                                    }}</span>
                                                                <div class="unique_dynamicdatalist">
                                                                    <ul class="listOfProduct" v-if="IsDDLShowHide">
                                                                        <li class="listitem all-items"
                                                                            v-for="(item, index) in UniqueItemData"
                                                                            v-on:click="uniqueItemChange(item)"
                                                                            :value="item.Value" :key="index">
                                                                            {{ item.Name }}</li>
                                                                    </ul>
                                                                </div>
                                                            </div>
                                                            <a class="btn float-left" href="javascript:;"
                                                                v-on:click="itemDDLShowHide"
                                                                style="background-color: #fff;color: #303030;border-color: #abb5bf;border-left: none;max-height: 37px;"
                                                                id="customddlinput">
                                                                <em class="dropdown-toggle float-right"></em>
                                                            </a>
                                                        </Field>
                                                    </div>

                                                </div>
                                                <div
                                                    class="col-sm-8 col-md-8 col-lg-3 float-left pl-md-0 mt-0 mt-md-4 mt-sm-4 pr-0">
                                                    <div class="background-0 text-left w-100 form-check-inline">
                                                        <input type="hidden" id="hdnSearch" value="">
                                                        <a title="Search" @click="SearchAssetByProduct()"
                                                            href="javascript:;"
                                                            class="btn btn-success w-50 mr-1 px-4 hlsearch"
                                                            data-toggle-tooltip="tooltip"
                                                            data-original-title="Search"><em
                                                                class="fa fa-search"></em></a>
                                                        <a id="aClear" href="javascript:;" title="Clear all "
                                                            v-on:click="ClearhdnIdsOnChange()"
                                                            class="btn btn-danger w-50 clearsearchtext px-4"
                                                            data-toggle-tooltip="tooltip"
                                                            data-original-title="Clear"><em
                                                                class="fa fa-refresh"></em></a>
                                                    </div>
                                                </div>
                                            </div>
                                        </Form>
                                    </template>

                                </div>
                                <AddrelationListing :ProductData="ProductData" :UniqueData="UniqueData"
                                    :isDataLoaded="isDataLoaded" :LocationValueForBarCode="LocationValueForBarCode"
                                    :iselectVal="iselectVal" :txtBarcode="txtBarcode" :txtAssetType="txtAssetType"
                                    :LocationValue="TreeValue" :AssetData="AssetData" :FetchData="FetchData"
                                    :ClosePopup="ClosePopup" v-if="searchResult" :IsProduct="IsProduct"
                                    :checkItem="checkItem" :assignTillDate="assignTillDate"
                                    :CreateUniqueNameListing="CreateUniqueNameListing"
                                    :AssetCatalogueId="AssetCatalogueId">
                                </AddrelationListing>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import DataService from '../../service/DataService';
import AddrelationListing from '../../components/Modal/AddRelationListing.vue';
import Treeselect from '@riophae/vue-treeselect'
import '@riophae/vue-treeselect/dist/vue-treeselect.css'
export default {
    components: {
        AddrelationListing,
        'tree-view': Treeselect
    },
    props: {
        FetchData: {
            type: Function
        }
    },
    data() {
        return {
            SearchByBarcode: true,
            IsProductName: true,
            SearchByProduct: false,
            LocationData: null,
            TreeValue: null,
            LocationValueForBarCode: '',
            AssetTypeData: [],
            txtBarcode: null,
            txtAssetType: '',
            searchResult: false,
            isLoading: false,
            IsDDLShowHide: false,
            assetUniqueItem: '',
            iselectVal: null,
            UniqueItemData: [],
            LocationName: '',
            ProductData: [],
            options: [],
            isuniqueSearch: 'No',
            AssetData: [],
            UniqueData: [],
            filterRecodes: [],
            SuniqueItemData: [],
            showProductDropdown: false,
            IsProduct: 'ProductName',
            assignTillDate: null,
            checkItem: null,
            isDataLoaded: false,
            UniqueItems: [],
            AssetCatalogueId: null,
            CreateUniqueNameListing: false

        }
    },
    created: function () {
        var vm = this;
        vm.searchByProduct('barcode');
        vm.GetLocationByCompanyId();

    },
    methods: {

        GetLocationDataByBarcode: async function () {
            var vm = this;
            vm.isLoading = true;
            var url = `barcode=${this.txtBarcode}&reqFrom=addAssetRelationShip`;
            await DataService.GetLocationByBarcode(url).then(response => {
                if (response.data[0] != null) {
                    vm.LocationName = response.data[0].LOCATION_NAME;
                    vm.LocationValueForBarCode = response.data[0].LOCATION_ID;
                    vm.isLoading = false;
                    // vm.GetAssetUniqueData();
                } else {
                    vm.isLoading = false;
                }
            })
        },
        searchByProduct: function (val) {
            var vm = this;
            this.SearchByBarcode = false;
            if (val == 'barcode') {
                vm.isdata = false;
                vm.searchResult = false;
                vm.SearchByBarcode = true;
                vm.CheckedCheckBox = 'SearchProductName';
                vm.BarcodeSelected = 'BarCodeSearch';
                vm.ProductData = [];
            } else {
                vm.isdata = true;
                vm.SearchByBarcode = false;
                vm.searchResult = false;
                vm.CheckedCheckBox = 'SearchProducts';
                vm.BarcodeSelected = 'ProductSearch';
                vm.ProductData = [];
            }
            //this.ClearhdnIdsOnChange();
        },
        ClearhdnIdsOnChange: function () {
            var vm = this;
            vm.txtBarcode = '';
            vm.ProductData = [];
            vm.LocationValueForBarCode = '',
                vm.TreeValue = null,
                vm.assetUniqueItem = '';
            vm.searchByProduct('barcode');
        },
        itemDDLShowHide: function () {
            this.IsDDLShowHide = !this.IsDDLShowHide;
        },
        uniqueItemChange: function (item) {
            var vm = this;
            vm.iselectVal = item.Value;
            vm.assetUniqueItem = item.Name;
            vm.IsDDLShowHide = false;
        },
        searchByItemName: function (val) {
            var vm = this;
            vm.IsProductName = false;
            if (val == 'ProductName') {
                vm.IsProductName = true;
                vm.IsProduct = 'ProductName';
                vm.assetUniqueItem = '';
                vm.searchResult = false;

            } else {
                vm.IsProductName = false;
                vm.searchResult = false;
                vm.IsProduct = 'Product';
            }
        },

        BindItemTypeByUserId: async function (ths) {
            var vm = this;
            vm.isLoading = true;
            if (typeof (ths) == 'undefined') {
                vm.isLoading = false;
                return;
            } else {
                var url = `id=&isView=&barcode=&assetType=&assetUniqueId=&selfCheckout=&reqFrom=addAssetRelationShip&isFor=&isuniqueSearch=&issueTo=&assignedFrom=&assignedTill=&startTime=&endTime=&locationId=${vm.TreeValue}&RFID=&pageSize=&pageNum=&assignedFromDateTime=&assignedTillDateTime=&uniqueIds=&catalogueIds=`;
                await DataService.GetCompanyAssestCatalogsDetalis(url).then(response => {
                    vm.AssetTypeData = response.data.Result;
                    vm.GetAssetUniqueData();
                    vm.isLoading = false;
                });
            }
        },
        GetLocationByCompanyId: async function () {
            var vm = this;
            var url = `reqForm=assetassignment`
            await DataService.GetLocationByuserId(url).then(response => {
                vm.FindalLocationArray = [];
                response.data.data.forEach((value) => {
                    var CheckIfExist = vm.FindalLocationArray.filter(function (el) {
                        return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                    });
                    if (CheckIfExist.length == 0) {
                        if (value.PARENT_LOCATION_ID == null) {
                            let locationId = value.COMPANY_LOCATION_ID
                            let rslt = vm.getChildLocation(locationId, value, response.data.data)
                            vm.FindalLocationArray.push(rslt)
                        }
                    }
                });
                vm.options = vm.FindalLocationArray;
            });
        },
        getChildLocation: function (locationId, currentItem, data) {
            let ths = this;
            let treeArray = {};
            treeArray = {
                id: currentItem.COMPANY_LOCATION_ID,
                label: currentItem.LOCATION_NAME
            }
            let retrunArray = treeArray;
            var childArray = data.filter(function (el) {
                return el.PARENT_LOCATION_ID == locationId;
            });
            let finalChildArray = [];
            childArray.forEach((value) => {
                let ChildfolderId = value.COMPANY_LOCATION_ID
                var CheckIfExist1 = finalChildArray.filter(function (el) {
                    return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                });
                if (CheckIfExist1.length == 0) {
                    var CheckIfExist = data.filter(function (el) {
                        return el.PARENT_LOCATION_ID == ChildfolderId;
                    });
                    if (CheckIfExist != null && CheckIfExist.length > 0) {
                        let rslt = ths.getChildLocation(ChildfolderId, value, data);
                        finalChildArray.push(rslt);
                    } else {
                        treeArray = {
                            id: `${value.COMPANY_LOCATION_ID}`,
                            label: `${value.LOCATION_NAME}`
                        }
                        finalChildArray.push(treeArray);
                    }
                }
            });
            retrunArray.children = finalChildArray;
            return retrunArray;
        },
        ClosePopup() {
            this.$parent.CloseAddRelationShipPopup();
        },



        // SearchAssetByBarCode: async function () {
        //     var vm = this;
        //     vm.isLoading = true;
        //     vm.isuniqueSearch = 'yes';

        //     if (vm.LocationValueForBarCode == null) {
        //         vm.isLoading = false;
        //     }
        //     vm.getNonUniqueProductsByBarcodeLocation();

        //     var url = `id=&catalogueIds=&uniqueIds=&isView=1&assetType=&barcode=${vm.txtBarcode}&assetUniqueId=&issueTo=&reqFrom=addAssetRelationShip&isFor=search&assignedFrom=&assignedTill=&startTime=&endTime=&locationId=${vm.LocationValueForBarCode}&RFID=&isuniqueSearch=no`;
        //     vm.$refs.ProductSearch.validate().then(result => {
        //         if (result) {
        //             DataService.GetCompanyAssestCatalogsDetalis(url).then(response => {

        //                 debugger;
        //                 if (response.data.length > 0) {
        //                     vm.CreateUniqueNameListing = response.data[0]?.CreateUniqueNameListing;
        //                     if (vm.CreateUniqueNameListing == true) {
        //                         let relatedInfoId = response.data[0]?.ParentUniqueId;
        //                         let LocationId = response.data[0]?.LocationId;
        //                      await   vm.getUniqueAssetItemsExceptItemName(relatedInfoId, LocationId);
        //                     }


        //                     vm.ProductData = response.data;
        //                     vm.AssetCatalogueId = response.data[0]?.AssetCatalogueId;

        //                     let isValidation = true;
        //                     let count = 1;
        //                     let isMatchRecord = false;
        //                     vm.AssetData = [];
        //                     vm.AssetTypeData.map((item) => {
        //                         isMatchRecord = vm.ProductData.some(x => x.AssetCatalogueId == item.Value);
        //                         if (isMatchRecord == false) {
        //                             vm.AssetData.push(item);
        //                         } else {
        //                             console.log("isMatchRecord", isMatchRecord)
        //                         }
        //                     })
        //                     vm.ProductData.map(ele => {
        //                         ele.isValidation = isValidation;
        //                         ele.count = count;
        //                     })
        //                     vm.isLoading = false
        //                     vm.searchResult = true;
        //                 } else {
        //                     vm.searchResult = true;
        //                     vm.NoRecordLength = 0;
        //                     vm.ProductData = [];
        //                     vm.isLoading = false;
        //                 }

        //             })
        //         }
        //     })
        // },


        SearchAssetByBarCode: async function () {
            var vm = this;
            vm.isLoading = true;
            vm.isuniqueSearch = 'yes';

            if (vm.LocationValueForBarCode == null) {
                vm.isLoading = false;
            }

            var url = `id=&catalogueIds=&uniqueIds=&isView=1&assetType=&barcode=${vm.txtBarcode}&assetUniqueId=&issueTo=&reqFrom=addAssetRelationShip&isFor=search&assignedFrom=&assignedTill=&startTime=&endTime=&locationId=${vm.LocationValueForBarCode}&RFID=&isuniqueSearch=no`;
            vm.$refs.ProductSearch.validate().then(async (result) => {
                if (result) {
                    let response = await DataService.GetCompanyAssestCatalogsDetalis(url);
                    if (response.data.length > 0) {

                        vm.CreateUniqueNameListing = response.data[0]?.CreateUniqueNameListing;
                        if (vm.CreateUniqueNameListing == true) {
                            let relatedInfoId = response.data[0]?.ParentUniqueId;
                            let LocationId = response.data[0]?.LocationId;
                            await vm.getUniqueAssetItemsExceptItemName(relatedInfoId, LocationId);
                        } else {
                            await vm.getNonUniqueProductsByBarcodeLocation();
                        }

                        vm.ProductData = response.data;
                        vm.AssetCatalogueId = response.data[0]?.AssetCatalogueId;

                        let isValidation = true;
                        let count = 1;

                        let PairProduct = [];

                        let obj = {
                            pairProductName: '',
                            pairProductId: null
                        }

                        PairProduct.unshift(obj);

                        vm.ProductData.map(ele => {
                            ele.isValidation = isValidation;
                            ele.count = count;
                            ele.IsDDLShowHide = false;
                            ele.PairProductDetails = PairProduct;
                            if (ele.CreateUniqueNameListing == true) {
                                ele.Name = ele.AssetCatalogName + '(' + ele.AssetUniqueName + ')'
                            } else {
                                ele.Name = ele.AssetCatalogName;
                            }
                        })

                        let isMatchRecord = false;
                        vm.AssetData = [];
                        vm.AssetTypeData.forEach((item) => {
                            isMatchRecord = vm.ProductData.some(x => x.AssetCatalogueId == item.Value);
                            if (!isMatchRecord) {
                                vm.AssetData.push(item);
                            } else {
                                console.log("isMatchRecord", isMatchRecord);
                            }
                        });

                        vm.isLoading = false;
                        vm.searchResult = true;
                    } else {
                        vm.searchResult = true;
                        vm.NoRecordLength = 0;
                        vm.ProductData = [];
                        vm.isLoading = false;
                    }
                }
            });
        },




        Filterditems: async function () {
            let vm = this;
            vm.filterRecodes = [];

            let url = '';
            if (vm.assetUniqueItem !== "") {
                if (vm.TreeValue == null) {
                    vm.isLoading = false;
                    vm.assetUniqueItem = '';
                    vm.ShowAlert(
                        vm.$t("Please Select location First"),
                        "warning",
                        vm.$t("Alert")
                    );
                    return;
                }
                const searchTerm = vm.assetUniqueItem.toLowerCase();
                if (vm.LocationValueForBarCode == null || vm.LocationValueForBarCode == '') {
                    url = `assetCatalogId=&name=${vm.assetUniqueItem}&issueTo=&locationId=${vm.TreeValue}&requestFrom=addAssetRelationShip&assetType=`;
                } else {
                    url = `assetCatalogId=&name=&issueTo=&locationId=${vm.LocationValueForBarCode}&requestFrom=addAssetRelationShip&assetType=`;
                }
                await DataService.GetUniqueAssetItems(url).then(function (response) {
                    vm.filterRecodes = response.data;
                    vm.SuniqueItemData = response.data;
                    if (vm.filterRecodes != null) {
                        if (vm.filterRecodes.length == 1) {
                            vm.IsDDLShowHide = true;
                            vm.uniqueItemChange(...vm.filterRecodes);
                        } else {
                            vm.UniqueItemData = vm.filterRecodes;
                            vm.IsDDLShowHide = true;
                        }
                        vm.isLoading = false;
                    } else {
                        vm.isLoading = false;
                    }
                })


            } else {
                vm.isLoading = false;
            }
        },

        GetAssetUniqueData: async function () {
            var vm = this;
            let url = '';
            vm.UniqueItemData = [];
            if (vm.LocationValueForBarCode == null || vm.LocationValueForBarCode == '') {
                url = `assetCatalogId=&name=&issueTo=&locationId=${vm.TreeValue}&requestFrom=addAssetRelationShip&assetType=`;
            } else {
                url = `assetCatalogId=&name=&issueTo=&locationId=${vm.LocationValueForBarCode}&requestFrom=addAssetRelationShip&assetType=`;
            }
            await DataService.GetUniqueAssetItems(url).then(function (response) {
                vm.UniqueItemData = response.data;
                vm.SuniqueItemData = response.data;
            })
        },

        SearchAssetByProduct: async function () {
            let url = '';
            var vm = this;
            vm.isLoading = true;
            vm.AssetData = [];
            vm.UniqueData = [];

            if (vm.txtAssetType != '') {
                vm.AssetTypeData.forEach((item) => {
                    if (item.Value != vm.txtAssetType) {
                        vm.AssetData.push(item);
                    }
                })
            }

            if (vm.iselectVal == null) {
                url = `id=${vm.txtAssetType}&isView=1&barcode=&assetType=&assetUniqueId=&selfCheckout=&reqFrom=addAssetRelationShip&isFor=search&isuniqueSearch=no&issueTo=&assignedFrom=&assignedTill=&&startTime=&endTime=&locationId=${vm.TreeValue}&RFID=&pageSize=&pageNum=&assignedFromDateTime=&assignedTillDateTime=&uniqueIds=&catalogueIds=${vm.txtAssetType}`;
            } else {
                vm.ProductData = []
                url = `id=&isView=1&barcode=&assetType=0&assetUniqueId=${vm.iselectVal}&selfCheckout=&reqFrom=addAssetRelationShip&isFor=search&isuniqueSearch=no&issueTo=&assignedFrom=&assignedTill=&&startTime=&endTime=&locationId=${vm.TreeValue}&RFID=&pageSize=&pageNum=&assignedFromDateTime=&assignedTillDateTime=&uniqueIds=${vm.iselectVal}&catalogueIds=`;

            }
            vm.getUniqueAssetItemsExceptItemName();
            vm.$refs.ProductSearchBar.validate().then(result => {
                if (result) {
                    DataService.GetCompanyAssestCatalogsDetalis(url).then(response => {
                        if (response.data.length > 0) {

                            vm.ProductData = response.data;
                            vm.CreateUniqueNameListing = response.data[0]?.CreateUniqueNameListing;
                            vm.AssetCatalogueId = response.data[0]?.AssetCatalogueId;
                            let isValidation = true;

                            let count = 1;

                            let PairProduct = [];

                            let obj = {
                                pairProductName: '',
                                pairProductId: null
                            }

                            PairProduct.unshift(obj);

                            vm.ProductData.map(ele => {
                                ele.isValidation = isValidation;
                                ele.count = count;
                                ele.IsDDLShowHide = false;
                                ele.PairProductDetails = PairProduct;
                                if (ele.CreateUniqueNameListing == true) {
                                    ele.Name = ele.AssetCatalogName + '(' + ele.AssetUniqueName + ')'
                                } else {
                                    ele.Name = ele.AssetCatalogName;
                                }
                            })

                            vm.isLoading = false;
                            vm.searchResult = true;

                            return;
                        } else {
                            vm.isLoading = false
                            if (response.data.checkItem == 1 || response.data.checkItem == 2) {
                                vm.checkItem = null;
                                vm.assignTillDate = null;
                                vm.checkItem = response.data.checkItem;
                                vm.assignTillDate = response.data.assignTillDate;
                            } else {
                                vm.searchResult = true;
                                vm.NoRecordLength = 0;
                            }
                            vm.searchResult = true;
                            vm.NoRecordLength = 0;
                            vm.ProductData = []
                        }
                        vm.isDataLoaded = true;
                        return vm.isLoading = false;
                    })



                } else {
                    vm.isLoading = false;

                }

            })

        },

        async getUniqueAssetItemsExceptItemName(relatedInfoId, LocationId) {
            var vm = this;
            var url = `relatedInfoId=${vm.iselectVal == null ? relatedInfoId : vm.iselectVal}&name=&issueTo=&locationId=${vm.TreeValue == null ? LocationId : vm.TreeValue}&requestFrom=ASSETREQUISITION&assetType=`;
            await DataService.GetUniqueAssetItemsExceptItemName(url).then((response) => {
                vm.UniqueItems = response.data;
                vm.UniqueItems.forEach((item) => {
                    if (item.Value != vm.iselectVal) {
                        vm.UniqueData.push(item);
                    }
                })
                    ;

            })
        },

        async getNonUniqueProductsByBarcodeLocation() {
            var vm = this;
            var url = `id=&isView=&barcode=&assetType=&assetUniqueId=&selfCheckout=&reqFrom=addAssetRelationShip&isFor=&isuniqueSearch=&issueTo=&assignedFrom=&assignedTill=&startTime=&endTime=&locationId=${vm.LocationValueForBarCode}&RFID=&pageSize=&pageNum=&assignedFromDateTime=&assignedTillDateTime=&uniqueIds=&catalogueIds=`;
            await DataService.GetCompanyAssestCatalogsDetalis(url).then(response => {
                vm.AssetTypeData = response.data.Result;
            });
        }
    }
}
</script>

