<template>
    <section>
        <tg-list :IsShowAction="false" :listType="['List']" ModuleName="Asset" SubModuleCode="Asset"
            :showCheckBox="true" :PageSize="PageSize" IdentityColumn="AssetRequisitionId"
            :HeaderText="$t('NEW_ASSET_REQUISITION')" :ListData="ProductData" :HeaderData="Headers" :widgets="widgets"
            :callbackfunction="FetchData" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecord"
            :SearchFields="leftSearchFields" :RenderRowActionMethod="renderActions"
            :ListDataCallBackFunction="FetchData" :LegendArray="LegendArray" :SortExp="SortExp"
            :NorecordfoundText="$t('NoRecordfound')" :SortBy="SortBy" v-on:LeftsearchButtonClick="leftCommonSearch"
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
            v-on:ActionButtonClick="actionButtonClick" v-on:SortdataButtonClick="sortdata">


            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'CreatedAt'">
                    <span :title="$options.filters.formatDateTime(data.row.CreatedAt)">{{
            $options.filters.formatDateTime(data.row.CreatedAt) }}</span>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'StatusName'">
                    <span v-if="data.row.StatusName == 'Active'" class="px-2 bg-info text-center text-white d-block">
                        Active
                    </span>
                    <span v-else-if="data.row.StatusName == 'Inactive'"
                        class="px-2 bg-danger text-center text-white d-block">
                        Inactive
                    </span>
                    <span v-else-if="data.row.StatusName == 'Draft'"
                        class="px-2 bg-success text-center text-white d-block">
                        Draft
                    </span>
                    <span
                        v-else-if="data.row.StatusName == 'Asset Requisition Cancelled By Assigner' || 'Asset Requisition Cancelled By User'"
                        class="px-2 bg-danger text-center text-white d-block">
                        Rejected
                    </span>
                    <span v-else class="px-2 bg-success text-center text-white d-block">
                        Assigned
                    </span>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'View'">
                    <a class="aViewRequisitionQuote" title="View" data-toggle-tooltip="tooltip"
                        data-original-title="View">
                        <!-- <span
                            v-if="data.row.HasQuoteNewRequisitionList == true || data.row.HasPurchaseNewRequisitionList == true">
                            <em class="fa fa-eye text-info action-icon" v-on:click="viewRequest(data.row)"></em>
                        </span>
                        <span v-else>
                            <span class="fa fa-eye text-info action-icon disabled" v-on:click="viewRequest(data.row)"></span>
                        </span> -->

                        <span>
                            <span class="fa fa-eye text-info action-icon" v-on:click="viewRequest(data.row)"></span>
                        </span>
                        <!-- <span>
                            <span class="fa fa-eye text-info action-icon" v-on:click="viewRequestPurchase(data.row)"></span>
                        </span> -->
                    </a>
                </template>
            </template>
        </tg-list>
        <RelationPopup v-if=IsShowRelation></RelationPopup>
        <RequisitionViewModel v-if=IscheckApproveData @ClosePOPuP="ClosePOPuP" :RequisitionData="RequisitionData">
        </RequisitionViewModel>
        <NewRequisitionViewPurchase v-if=IscheckApprovePurchaseData @ClosePOP="ClosePOP"
            :RequisitionData="RequisitionData">
        </NewRequisitionViewPurchase>
    </section>
</template>

<script>
import Schema from "../AssetRequisition/NewAssetRequisitionListingSchema"
import DataService from '../../service/DataService'
import RelationPopup from '../../components/Modal/AddRelationPopup.vue'
import RequisitionViewModel from '../../components/Modal/NewRequisitionViewModel.vue'
import NewRequisitionViewPurchase from '../../components/Modal/NewRequisitionViewPurchaseModel.vue'
export default {
    components: {
        RelationPopup,
        RequisitionViewModel,
        NewRequisitionViewPurchase
    },
    data() {
        return {
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            LegendArray: [],
            ProductData: [],
            PageSize: 10,
            PageNumber: 1,
            PageSizeOption: null,
            StatusListForListing: [],
            Headers: [{
                "COLUMN_NAME": "ProductName",
                "DATA_TYPE": "image",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('Product Name'),
                "DISPLAY_ORDER": 1,
                settings: {
                    clickEvent: this.clickEventOfColumn,
                    formatter: this.columnDataFormatter,
                }
            },
            {
                "COLUMN_NAME": "AssetType",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('Product Type'),
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": "Quantity",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('Requested Quantity'),
                "DISPLAY_ORDER": 3
            },
            {
                "COLUMN_NAME": "StatusName",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Status",
                "DISPLAY_ORDER": 10,
                settings: {
                    isInSlot: true
                }

            },
            {
                "COLUMN_NAME": "Description",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('Description'),
                "DISPLAY_ORDER": 4,



            },
            {
                "COLUMN_NAME": "CreatedBy",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('Created By'),
                "DISPLAY_ORDER": 5,

            },
            {
                "COLUMN_NAME": "CreatedAt",
                "DATA_TYPE": "Date",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('CreatedAt'),
                "DISPLAY_ORDER": 6,
                settings: {
                    isInSlot: true
                }
            },


            {
                "COLUMN_NAME": "View",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('View'),
                "DISPLAY_ORDER": 7,
                settings: {
                    isInSlot: true
                }
            }


            ],
            noRecord: true,
            TotalRecord: 0,
            CurrentPage: 1,
            TotalPages: 0,
            LocationId: '',
            SortBy: '',
            SortExp: '',
            isPaged: 1,
            searchCondition: "",
            widgets: [

            ],
            legendProgress: [],
            IsShowRelation: false,
            IscheckApproveData: false,
            IscheckApprovePurchaseData: false,
            RequisitionData: []
        }
    },
    async created() {
        var vm = this;
        await vm.GetPageSize();
        Schema.bindleftCommonSearchdropdown(vm, 'AssetRequisition', 'Product Type');
        vm.FetchData();

    },

    methods: {
        columnDataFormatter: function (row, column) {
            return row[column.COLUMN_NAME];
        },
        async GetPageSize() {
            var vm = this;
            var params = 'key=DefaultPageSize'
            await DataService.GetConfigurationValueForCompanyByKey(params).then((response) => {
                if (response.data != null) {
                    vm.PageSize = parseInt(response.data);
                }
            });
        },
        FetchData: function () {
            var vm = this;
            vm.isLoading = true;
            // debugger
            var params = `pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${this.SortExp}&${vm.searchCondition}`;
            DataService.NewAssetRequisitionListing(params).then(response => {
                if (response.data != null) {
                    if (response.data.length > 0) {
                        vm.ProductData = response.data;
                        vm.TotalRecord = response.data[0].Total;
                        vm.TotalPage = Math.ceil(vm.TotalRecord / vm.PageSize);
                        vm.CurrentPage = vm.PageNumber;
                        vm.noRecord = false;
                        vm.isLoading = false;
                        vm.listheaderbuttons[2].isdisabled = true;
                    }
                    else {
                        vm.noRecord = true;
                        vm.ProductData = [];
                        vm.TotalRecord = 0
                        vm.isLoading = false;
                    }
                }

            });
        },

        clickEventOfColumn: function (row, column) {
            this.$router.push({ name: "AddNewRequisition", params: { reqForm: 'ProductTypeList', items: row } });
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchConditionJson;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        currentPageMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        GetPageSizeOption: function () {
            var list = [];
            var defaultPageSize = 10;
            for (var i = defaultPageSize; i < defaultPageSize + 95; i += 5) {
                list.push(i);
            }
            this.PageSizeOption = list;
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },

        commonHeaderButtonClick(event) {
            switch (event.callbackfunction) {
                case "RequestNewProductType":
                    this.RequestNewProductType();
                    break;
                case "setTourGuideSteps":
                    this.setTourGuideSteps(0);
                    break
                case "deleteItem":
                    this.deleteItem();
                    break

            }
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }

            this.FetchData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },
        actionButtonClick() {

        },
        renderActions(row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                // switch (action.title) {
                //     case 'Edit':
                //         action.isVisible = true;
                //         break;
                // }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },

        deleteItem() {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });

            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("New_Requisition")), function () {
                    var postJSON = {
                        ids: val,
                        tblname: "TALYGEN_new_asset_requisition",
                        primarycolname: "asset_requisition_id"
                    };
                    vm.isLoading = true;
                    var poststing = JSON.stringify(postJSON);
                    DataService.NewAssetRequisitionDelete(poststing).then(response => {
                        if (response.data) {                    
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('New_Requisition')]), "Success", true, vm.$t('Alert'));
                            vm.FetchData();
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('New_Requisition')]), "failure", true, vm.$t('Alert'));
                        }
                    });
                });
            }

        },

        RequestNewProductType() {
            this.$router.push({ path: '/AssetRequisition/AddNewRequisition' })

        },
        viewRequest(item) {
            var vm = this;
            vm.IscheckApproveData = true;
            vm.RequisitionData = item;

        },
        viewRequestPurchase(item) {
            var vm = this;
            vm.IscheckApprovePurchaseData = true;
            vm.RequisitionData = item;

        },
        ClosePOPuP: function () {
            var vm = this;
           // vm.IsChecked();
            vm.IscheckApproveData = false;
        },
        ClosePOP: function () {
            var vm = this;
           // vm.IsChecked();
            vm.IscheckApprovePurchaseData = false;
        },
        IsChecked() {
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                setTimeout(() => {
                    $('#DeleteMultiple').removeClass('disabled');
                }, 100);
            }
        }
    },

}

</script>