let leftsearchSchema = [
    {
    fieldName: "Product Name",
    fieldType: "text",
    fieldIcon: "fa-file",
    paramName:"search",
    fieldSearchConditionName: [''],
    isSearch: false,
    value: "",
    listOptions: []
}, 
{

    fieldName: 'Product Type',
    fieldType: "ddl-check",
    fieldIcon: "fa-user",
    paramName:"assettypeid",
    fieldSearchConditionName: [''],
    isSearch: false,
    value: "",
    listOptions: []
}

]


 
function bindleftCommonSearchdropdown(instance,modulename, filtername) {
 

    var vm = instance;
    let productTypeData=[];
    if (filtername == "Product Type") {
        vm.isLoading = true;
        var url = `type=ITEMCODE&issueTo=&locationId=`;
         DataService.GetAssetTypeCustom(url).then(response => {

            if(response.data!==null){

                let obj={}

                JSON.parse(response.data).forEach(item => {
                    obj={
                    name:item.ASSETTYPE,
                    value:`${item.ASSETID}`
                    }
                    productTypeData.push(obj);
                });
                vm.leftSearchFields[1].listOptions =productTypeData;
                

            }
           
         
        })
        
    }



}
export default { buttons,leftsearchSchema,bindleftCommonSearchdropdown};